<template>
  <button type="button" aria-label="Добавить один товар" @click="addProductItem(item.amount)">
    <svg width="12" height="12" fill="currentColor">
      <use xlink:href="#icon-plus"></use>
    </svg>
  </button>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    addProductItem(value) {
      this.$store.dispatch('addProductToItem', { productId: this.item.productId, amount: value });
    },
  },
};
</script>
