<template>
  <button type="button" aria-label="Убрать один товар"
      @click.prevent="deleteProductItem(item.amount)">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
  </button>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    deleteProductItem(value) {
      this.$store.dispatch('remuveProductToItem', { productId: this.item.productId, amount: value });
    },
  },
};
</script>
