<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.imge" width="120" height="120"
        :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{item.product.title}}
    </h3>
    <span class="product__code">
      Артикул: {{item.product.id}}
    </span>

    <div class="form__counter">
      <BtnMines :item="item" />

      <input type="text" v-model.number="amount">

      <BtnPlus :item="item" />
    </div>

    <b class="product__price">
      {{(item.amount * item.product.price) | numberFormat}} ₽
    </b>

    <button class="product__del button-del" type="button"
    aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.product.id)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BtnPlus from '@/components/BtnPlus.vue';
import BtnMines from '@/components/BtnMines.vue';

export default {
  components: { BtnPlus, BtnMines },
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    deleteProduct(productId) {
      this.$store.dispatch('deleteCartProduct', productId);
    },
  },
};
</script>
